	mounted(){
		// 小程序中,路由传参通过onLoad的options参数接受
		//uniapp中,路由传参通过this.$mp获取
		let userInfo= this.$mp.query.userInfo;
		if(userInfo){
			userInfo=JSON.parse(this.$mp.query.userInfo);
			this.userInfo = userInfo;
		}else{
			uni.getUserInfo({
				success:(res)=>{
					// console.log(res)
					this.userInfo= res.userInfo;
				}
			})
		}
